import React, {
  useEffect, useMemo, useState, useRef, useContext,
} from 'react'
import DSSearchBox from '@elliemae/ds-basic/form/SearchBox'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import { focusGroupManagerHoc } from '@elliemae/ds-shared/FocusGroup'
import PropTypes from 'prop-types'
import VirtualMenuList from '../VirtualMenuList'
import MenuContext from '../MenuContext'

const SearchBoxWrapper = aggregatedClasses('div')('menu-search-group', 'search-wrapper')
const NoOptionsWrapper = aggregatedClasses('div')('menu-search-group', 'no-options')

function SearchableGroup({
  children,
  amountItemsInWindow = 5,
  searchTerm: searchTermProp = '',
  noOptionsText = 'No options',
  height,
  width,
  focusOnOpen = false,
}) {
  const searchRef = useRef(null)
  const [searchTerm, setSearchTerm] = useState(searchTermProp)

  useEffect(() => {
    if (searchRef.current) searchRef.current.focus()
  }, [])

  const { visible } = useContext(MenuContext)

  useEffect(() => {
    if (focusOnOpen && visible) {
      setTimeout(() => {
        if (searchRef.current) searchRef.current.focus()
      }, 0)
    }
  }, [focusOnOpen, visible])


  const filteredChildren = useMemo(
    () => (
      children.filter(child => (
        !searchTerm || child.props.label.toLowerCase().startsWith(searchTerm.toLowerCase())),
      )),
    [children, searchTerm])

  return (
    <>
      <SearchBoxWrapper>
        <DSSearchBox
          innerRef={searchRef}
          onSearch={({ value }) => setSearchTerm(value)}
          searchOnEnter={false}
          showIcon={false}
          value={searchTerm}
        />
      </SearchBoxWrapper>
      {
        !filteredChildren.length
        && <NoOptionsWrapper>{noOptionsText}</NoOptionsWrapper>
      }
      <VirtualMenuList
        amountItemsInWindow={amountItemsInWindow}
        height={height}
        items={filteredChildren}
        width={width}
      />
    </>
  )
}

SearchableGroup.propTypes = {
  /** The amount of items that are going to be rendered in the group */
  amountItemsInWindow: PropTypes.number,
  /** A search term */
  searchTerm: PropTypes.string,
  /** Text to show when there's no options to show */
  noOptionsText: PropTypes.string,
}

export default focusGroupManagerHoc(SearchableGroup)
